.employees__wrapper {
  width: 100%;
  margin-bottom: 30px;
}

.employees__top {
  background-color: @color-background;
  padding-top: 30px;
  padding-bottom: 10px;
}

.employees__wrapper-inner {
}

.employees__title {
  margin-bottom: 15px;
}

.employees__switch {
  margin-bottom: 15px;
}

.employees__btn {
  font-size: 15px;
  line-height: 1;
  padding-bottom: 13px;
  border: none;
  color: #808e96;
  background-color: transparent;
  border-bottom: 2px solid transparent;
  transition: 0.3s;
  outline: transparent;
}

.employees__btn--active {
  border-bottom: 2px solid #66a0e0;
  color: #1d1d1b;
}

.employees__projects-btn-wrapper {
  border: 1px solid @color-border;
  border-radius: 3px;
  min-height: 82px;
  box-sizing: border-box;
  padding-left: 14px;
  padding-top: 26px;
  padding-right: 14px;
  padding-bottom: 16px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-content: space-between;

  @media screen and (max-width: @small) {
    flex-wrap: nowrap;
    width: 93vw;
    overflow-x: scroll;
    border: none;
  }
}

.employees__workspace-controls {
  @media screen and (max-width: @small) {
    flex-wrap: wrap;
    margin-bottom: 0;
  }

  .controls__add-btn {
    width: 200px;
    text-align: center;

    @media screen and (max-width: @small) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  .controls__edit-txt-btn {
    width: 200px;
    text-align: center;

    @media screen and (max-width: @small) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  .controls__exp-xls {
    @media screen and (max-width: @small) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  .close-btn {
    margin-left: auto;

    @media screen and (max-width: @small) {
      width: 100%;
      margin-left: 0;
      border-radius: 3px;

      &::after {
        content: "Удалить";
        width: 100%;
        background: none;
        font-weight: 700;
        text-align: center;
        color: #e30613;
      }
    }
  }
}

.employees__table {
  border-bottom: 1px solid @color-border;
  margin-bottom: 20px;

  @media screen and (max-width: @medium) {
    width: 96vw;
    flex-wrap: nowrap;
    overflow-x: scroll;
  }

  @media screen and (max-width: @small) {
    border-bottom: none;
    width: 100%;
    overflow-x: auto;

    .table-item--checkbox {
      border-bottom: none;
      position: absolute;
      z-index: 10;
    }
  }
}

.employees__table-title-row {
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(0deg, #e6eff3 0, #f4f8fa 100%);
  min-height: 54px;
  border-radius: 3px;

  @media screen and (max-width: @medium) {
    width: 1170px;
  }

  @media screen and (max-width: @small) {
    background: none;
    width: 100%;

    .employees__table-title-item {
      display: none;
    }

    .employees__table-title-item.table-item--checkbox {
      display: block;
      background: none;
      max-width: 93vw;

      .employees__mobile-title-txt {
        display: inline;
        font-weight: 400;
      }
    }
  }
}

.employees__table-title-item {
  font-weight: 700;
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 17px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #ffffff;
    position: absolute;
    top: 10%;
    right: 0;
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}

.employees__table-row {
  display: flex;
  justify-content: flex-start;
  min-height: 43px;
  border-bottom: 1px solid @color-border;
  border-left: 1px solid @color-border;
  border-right: 1px solid @color-border;

  &:last-child {
    border-bottom: 1px solid transparent;
  }

  @media screen and (max-width: @medium) {
    width: 1170px;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    flex-direction: column;
    margin-bottom: 20px;
    border-bottom: none;
  }
}

.employees__table-item {
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 11px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: @color-border;
    position: absolute;
    top: 10%;
    right: 0;
  }

  &:last-child {
    &::after {
      display: none;
    }
  }

  @media screen and (max-width: @small) {
    display: flex;
    border-top: 1px solid @color-border;
    border-bottom: 1px solid @color-border;
    max-width: 100%;
    padding-bottom: 10px;

    &::after {
      right: 50%;
    }

    &:first-child {
      &::after {
        display: none;
      }
    }

    &:last-child {
      &::after {
        content: "";
        display: block;
        height: 80%;
        width: 1px;
        background-color: @color-border;
        position: absolute;
        top: 10%;
        right: 50%;
      }
    }
  }
}

.employees__table-item--name {
  @media screen and (max-width: @small) {
    padding-left: 40px;
    padding-bottom: 15px;
    background-image: linear-gradient(0deg, #e6eff3 0, #f4f8fa 100%);

    &::after {
      display: none;
    }
  }
}

.employees__mobile-title-txt {
  display: none;

  @media screen and (max-width: @small) {
    display: block;
    font-weight: bold;
    color: @color-text;
    width: 50%;
  }
}

.employees__mobile-title-txt--name {
  display: none;
}

.table-item--checkbox {
  max-width: 42px;
  padding-left: 10px;
}

.employees__table-row--waiting {
  color: #f6a522;
}

.employees__table-row--denial {
  color: #e30613;
}

// настройки доступа


.employees__wrapper-inner--access-denail {
  .employees__table-title-row {
    border-radius: 3px 3px 0 0;
  }
}

.employees__sub-title {
  font-weight: 300;
  font-size: 25px;
  margin-bottom: 30px;
}

.employees__reset-btn {
  background-color: #ffffff;

  @media screen and (max-width: @small) {
    width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.employees__restore-settings-btn {
  @media screen and (max-width: @small) {
    width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.employees__save-btn {
  background-color: #ffffff;
  margin-left: auto;
  margin-right: 0;
}

.employees__table-managerial-row {
  display: flex;
  background-color: @color-background;
  min-height: 86px;
  border: 1px solid @color-border;
  margin-bottom: 10px;

  .employees__table-item span {
    display: block;
    margin-bottom: 15px;
    padding-top: 7px;
  }
}

.employess__messengers-label {
  font-size: 13px;
  display: inline-block;
  vertical-align: middle;
}

.employess__messengers-checkbox {
  display: inline-block;
  vertical-align: middle;
}

.employees__table-item--sel-mess {
  padding-left: 9px;
  padding-right: 9px;

  .cd-dropdown {
    margin-bottom: 10px;

    span {
      padding-top: 5px;
    }
  }
}

.employees__table-managerial-rows-wrapper {
  border: 1px solid @color-border;

  .employees__table-row {
    border-left: none;
    border-right: none;
  }
}

.employees--faded {
  display: none;
}

.employees__mobile-switch {
  display: none;

  @media screen and (max-width: @small) {
    display: block;
    margin-right: 10px;
    width: 50%;
  }

  .cd-dropdown__span {
    background-color: transparent;
    border: none;
  }
}

.employees__save-btn--mobile {
  display: none;
}

@media screen and (max-width: @medium) {
  .employees__form-table {
    width: 96vw;
    overflow-x: scroll;
  }

  .employees__table-managerial-rows-wrapper,
  .employees__table-managerial-row {
    width: 1170px;
  }
}

@media screen and (max-width: @small) {
  .employees__table-managerial-rows-wrapper,
  .employees__table-managerial-row {
    width: 100%;
  }

  .employees__save-btn {
    display: none;
  }

  .employees__save-btn--mobile {
    display: block;
    width: 100%;
  }

  .employees__table-item--moderator,
  .employees__table-item--menager,
  .employees__table-item--responsible {
    max-width: 45%;
    display: none;
    padding-left: 25px!important;
  }

  .employees__table-item--admin {
    max-width: 45%;
    padding-left: 25px!important;
  }

  .employees__form-table {
    overflow-x: auto;
    width: calc(100% + 30px);
    margin-left: -15px;
    border: none;

    .employees__table-title-row {
      border: none;
    }

    .employees__table-managerial-row {
      border: none;
      padding-bottom: 10px;

      .employees__table-item {
        padding-left: 20px;
        &::after {
          display: none!important;
        }
      }
    }

    .employees__table-title-item {
      &:first-child {
        display: block;
      }
    }

    .employees__table-item {
      flex-direction: column;
      padding-bottom: 10px;
      padding-left: 30px;

      &.form__option--waiting-activation {
        &::after {
          background-color: #626262;
        }
      }

      &.form__option--waiting-completion {
        &::after {
          background-color: #f6a522;
        }
      }

      &.form__option--failed {
        &::after {
          background-color: #861115;
        }
      }

      &.form__option--waiting-verification {
        &::after {
          background-color: #75daff;
        }
      }

      &.form__option--waiting-decision {
        &::after {
          background-color: #0166ff;
        }
      }

      &.form__option--made-tested {
        &::after {
          background-color: #0166ff;
        }
      }

      &.form__option--not-made-tested {
        &::after {
          background-color: #bd1ee1;
        }
      }

      &.form__option--urgent {
        &::after {
          background-color: #fff602;
        }
      }

      &.form__option--made-first {
        &::after {
          background-color: #c5343a;
        }
      }

      &::after {
        display: none;
      }

      &:first-child::after {
        content: "";
        display: block;
        width: 11px;
        height: 11px;
        position: absolute;
        top: 20px;
        transform: translateY(-50%);
        left: 15px;
        border-radius: 50%;
      }
    }

    .employees__table-row {
      flex-direction: row;
      border-left: none;
      border-right: none;
    }
  }

  .employees__form .employees__table-title-row {
    .employees__table-title-item:first-child {
      display: flex;
      justify-content: space-between;
      padding-left: 20px;
      max-width: 100%;
      background-image: linear-gradient(0deg, #e6eff3 0, #f4f8fa 100%);
    }
  }

  .employess__messengers-wrapper {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }

  .employees__table-managerial-rows-wrapper {
    border: none;
  }

  .employess__messengers-label {
    margin-bottom: 10px;
  }
}

