.project-pages {
  margin-bottom: 30px;

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.project-pages__wrapper-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;

  @media screen and (max-width: @small) {
    padding: 0;
  }
}

.project-pages__wrapper-middle {
  margin-top: -50px;

  @media screen and (max-width: @small) {
    margin-top: 0;
    width: calc(100% + 30px);
    margin-left: -15px;
  }
}

.project-pages__btn {
  font-size: 15px;
  line-height: 1;
  padding-bottom: 13px;
  border: none;
  color: #808e96;
  background-color: transparent;
  border-bottom: 2px solid transparent;
  transition: all ease 0.3s;
  outline: transparent;

  &:hover {
    opacity: 0.7;
  }

  @media screen and (max-width: @small) {
    font-size: 13px;
    padding-top: 13px;
  }
}

.project-pages__btn--active {
  border-bottom: 2px solid @blue;
  color: @color-text;
}

.project-pages__edit-wrapper {
  max-width: 425px;
  width: 100%;
  display: flex;
  margin-left: auto;

  .project-pages__edit-btn--cancellation {
    display: none;
  }

  .project-pages__edit-btn--save {
    display: none;
  }

  @media screen and (max-width: @small) {
    margin-top: 20px;
    justify-content: space-around;
    margin-bottom: 10px;
    padding: 0 20px;

    .project-pages__edit-btn--cancellation {
      margin-left: 0;
    }

    .project-pages__edit-btn--save {
      margin-right: 0;
    }
  }
}

.project-pages__edit-btn {
  transition: all ease 0.3s;
  max-width: 200px;
  width: 100%;

  &:hover {
    opacity: 0.7;
  }

  @media screen and (max-width: @small) {
    max-width: 150px;
    margin: 0 10px;
  }
}

.project-pages__edit-btn--edit {
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  margin-right: 20px;
  color: #66a0e0;
  padding: 10px 14px;
  background-color: transparent;
  display: block;
  margin-left: auto;

  @media screen and (max-width: @small) {
    max-width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}

.project-pages__edit-btn--cancellation {
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  margin-right: 15px;
  color: #66a0e0;
  padding: 10px 14px;
  background-color: transparent;

  @media screen and (max-width: @small) {
    margin-left: 20px;
  }
}

.project-pages__edit-btn--save {
  position: relative;
  color: #ffffff;
  background-color: #66a0e0;
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  padding: 10px 14px;

  @media screen and (max-width: @small) {
    margin-right: 20px;
  }
}

.project-pages__edit--active {
  .project-pages__edit-btn--edit {
    display: none;
  }

  .project-pages__edit-btn--cancellation {
    display: block;
  }

  .project-pages__edit-btn--save {
    display: block;
  }
}

.project-pages--comments-files {
  padding-left: 20px;
  padding-top: 15px;
  padding-right: 20px;
  padding-bottom: 45px;

  @media screen and (max-width: @small) {
    width: calc(100% + 30px);
    margin-left: -15px;
    padding: 0 15px;
  }
}

.project-pages__switch--comments-files {
  .project-pages__btn--active {
    border: none;
  }
}

.project-pages__switch--project-workspace {
  @media screen and (max-width: @small) {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}

.project-pages__item--active {
  @media screen and (max-width: @small) {
    margin-bottom: 30px;
  }
}
