.result-type-projects {

}

.result-type-projects__title-row {
  background-image: linear-gradient(0deg, #e6eff3 0, #f4f8fa 100%);
  min-height: 32px;
  padding-top: 5px;
  padding-left: 10px;
  box-sizing: border-box;
  display: flex;
}

.result-type-projects__title {
  width: 100%;
  max-width: 232px;
  box-sizing: border-box;
  position: relative;
}

.result-type-projects__title--litera {
  max-width: 80px;
}

.result-type-projects__title--name {
  max-width: 268px;
}

.result-type-projects__title--dscr {
  max-width: 352px;
}

.result-type-projects__title--info {
  max-width: 100%;
}

.result-type-projects__info-list {
  display: flex;
  justify-content: flex-start;
  border: 1px solid #dce8ef;
  border-top: 1px solid transparent;

  &:nth-child(2n) {
    background-color: @color-background;
  }
}

.result-type-projects__info-item {
  width: 100%;
  box-sizing: border-box;
  position: relative;
  padding: 18px 8px 18px 10px;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #dce8ef;
    position: absolute;
    top: 10%;
    right: 0;
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}

.result-type-projects__info-item--litera {
  max-width: 80px;
}

.result-type-projects__info-item--name {
  max-width: 268px;
}

.result-type-projects__info-item--dscr {
  max-width: 352px;
}

.result-type-projects__info-item--info {
  max-width: 100%;
}


.result-type-projects__info-list--active {
  border: 1px solid @blue;
  box-shadow: 0 20px 30px 0 rgba(18, 59, 59, 0.09);
  border-radius: 3px;
}

.type-projects__workspace-controls {
  .controls__arrow,
  .controls__close-btn,
  .controls__edit-btn {
    pointer-events: none;
  }
}

.type-projects__workspace-controls.controls--active {
  .controls__arrow,
  .controls__close-btn,
  .controls__edit-btn {
    pointer-events: auto;
  }

  .controls__arrow {
    border: 1px solid @blue;
  }

  .contols__arrow--up {
    &::after {
      content: "";
      display: block;
      width: 22px;
      height: 12px;
      background-image: url(../img/icons/controls-arrow-up-icon-active.png);
      background-size: cover;
      background-position: 50%;
      background-repeat: no-repeat;
      position: absolute;
      top: 13px;
      left: 8px;
    }
  }

  .contols__arrow--down {
    &::after {
      content: "";
      display: block;
      width: 22px;
      height: 12px;
      background-image: url(../img/icons/controls-arrow-down-icon-active.png);
      background-size: cover;
      background-position: 50%;
      background-repeat: no-repeat;
      position: absolute;
      top: 13px;
      left: 8px;
    }
  }
}
