.personal-area__wrapper-top {
  padding-top: 30px;
  margin-bottom: 30px;
}

.personal__title {
  margin-bottom: 20px;
}

.personal-area__wrapper-top--inner {
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.personal-area__user {
  max-width: 465px;
  width: 100%;

  @media screen and (max-width: @medium) {
    margin-right: 20px;
  }

  @media screen and (max-width: @small) {
    margin-right: 0;
  }
}

.personal-area__user-data {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 20px;
}

.personal-area__user-img {
  margin-right: 20px;
  border-radius: 50%;
  border: 2px solid @blue;
}

.personal-area__user-position {
  display: block;
}

.personal-area__user-name {
  display: block;
  font-size: 25px;
  line-height: 1;
}

.personal-area__user-contacts {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  @media screen and (max-width: @small) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.personal-area__user-telephone,
.personal-area__user-email {
  margin-right: 20px;

  span {
    display: block;

    &:first-child {
      font-size: 13px;
    }
  }
}

.personal-area__user-edit-btn {
  display: inline-block;
  margin: 24px 0;
  width: 202px;
  text-align: center;

  @media screen and (max-width: @medium) {
    width: 100%;
  }
}

.personal-area__sub-users-wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  @media screen and (max-width: @medium) {
    flex-direction: column;
    margin-bottom: 15px;
  }
}

.personal-area__sub-users-txt {
  font-size: 25px;
  margin-right: 10px;

  @media screen and (max-width: @medium) {
    margin-bottom: 15px;
  }
}

.personal-area__sub-users {
  width: 132px;
  text-align: center;

  @media screen and (max-width: @medium) {
    width: 100%;
    margin-right: 0;
  }
}

//  personal-info

.personal-area__info {
  max-width: 585px;
  width: 100%;
}

.personal-area__balance {
  display: flex;
  justify-content: flex-start;
  border-bottom: 1px solid @color-border;
  padding-bottom: 30px;

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }
}

.personal-area__balance-txt {
  font-size: 25px;
  margin-right: 20px;

  span {
    font-size: 40px;
    line-height: 1;
    font-weight: bold;
    color: @blue;
  }

  @media screen and (max-width: @medium) {
    margin-bottom: 10px;
  }
}

.personal-area__balance-btn {
  @media screen and (max-width: @medium) {
    margin-right: 0;
    text-align: center;
  }
}

.personal-area__info-list {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid @color-border;
}

.personal-area__info-list-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-top: 6px;
  padding-bottom: 6px;

  @media screen and (max-width: @medium) {
    flex-direction: column;
    align-items: flex-start;

    .personal-area__info-list-btn {
      order: 1;
      padding-left: 0;
      padding-top: 12px;
    }

    &:last-child {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }
}

.personal-area__info-list-bold-txt {
  font-weight: bold;
  margin-right: 20px;

  span {
    font-weight: 400;

    &::before {
      content: "\00A0";
    }
  }

  @media screen and (max-width: @medium) {
    margin-right: 0;
  }
}

.personal-area__info-list-btn {
  margin-right: 20px;
  background-color: transparent;
  border: none;
  font-weight: bold;
  color: @blue;
}

.personal-area__wrapper-bottom {
  margin-bottom: 30px;

  @media screen and (max-width: @medium) {
    width: 96vw;
    overflow-x: scroll;
  }

  @media screen and (max-width: @small) {
    width: 93vw;
  }
}

.personal-area__rate {
  border-radius: 3px;
  border: 1px solid @color-border;

  @media screen and (max-width: @medium) {
    width: 970px;
  }
}

.personal-area__rate-title-row {
  min-height: 58px;
  border-bottom: 1px solid @color-border;
  background-color: @color-background;
  display: flex;
}

.personal-area__rate-title-item--title {
  font-size: 25px;
  line-height: 1;
  flex-grow: 1;
  padding-left: 20px;
  padding-top: 18px;
  // max-width: 362px;
  width: 100%;

  @media screen and (max-width: @medium) {
    width: 100%;
  }

  @media screen and (max-width: @small) {
    max-width: 200px;
  }
}

.personal-area__rate-title-item--personal,
.personal-area__rate-title-item--start,
.personal-area__rate-title-item--business {
  width: 100%;
  // max-width: 272px;
  padding-top: 21px;
  text-align: center;

  span {
    font-weight: bold;
    line-height: 21px;
  }
}

.personal-area__rate-title-item--personal {
  color: #a0a0a0;
}

.personal-area__rate-title-item--start {
  color: @blue;
}

.personal-area__rate-title-item--business {
  color: #e30613;
}

.personal-area__rate-row {
  border-bottom: 1px solid @color-border;
  display: flex;

  &:nth-child(9) {
    border-bottom: none;
  }

  &:last-child {
    border-bottom: none;
  }

  .personal-area__rate-item:last-child {
    background-color: @color-background;
  }
}

.personal-area__rate-row--buttons {
  .personal-area__rate-item {
    padding-bottom: 30px;
  }
}

.personal-area__rate-item {
  padding-top: 9px;
  padding-bottom: 12px;
  // max-width: 272px;
  width: 100%;
  text-align: center;
  position: relative;

  &:first-child {
    padding-left: 20px;
    flex-grow: 1;
    max-width: 100%;
    text-align: left;

    @media screen and (max-width: @small) {
      max-width: 200px;
    }
  }
}

.personal-area__rate-item--tick {
  &::after {
    content: "";
    width: 14px;
    height: 9px;
    display: block;
    background-image: url(../img/icons/tick-icon.png);
    background-size: cover;
    background-position: 50%;
    position: absolute;
    top: 14px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.personal-area__rate-item--close {
  &::after {
    content: "";
    width: 10px;
    height: 10px;
    display: block;
    background-image: url(../img/icons/close-personal-icon.png);
    background-size: cover;
    background-position: 50%;
    position: absolute;
    top: 14px;
    left: 50%;
    transform: translateX(-50%);
  }
}

// попап

.personal-area-file-list-popup {
  width: 1170px;
  margin-left: -585px;
  min-height: 420px;
  padding: 50px;

  .controls__close-btn {
    position: absolute;
    top: 50px;
    right: 50px;
  }
}

.personal-area-file-list-popup__slogan {
  font-size: 25px;
  line-height: 1;
  margin-bottom: 30px;
}

.personal-area-files__form {
  padding: 0;
}

.personal-area-list__table-title-row {
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(0deg,#e6eff3 0,#f4f8fa 100%);
  min-height: 32px;
  border-radius: 3px 3px 0 0;
}

.personal-area-list__table-title-item {
  font-weight: 700;
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 7px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #fff;
    position: absolute;
    top: 10%;
    right: 0;
  }
}

.personal-area-list__table-row {
  display: flex;
  justify-content: flex-start;
  min-height: 43px;
  border-bottom: 1px solid @color-border;
  border-left: 1px solid @color-border;
  border-right: 1px solid @color-border;
}

.personal-area-list__table-item {
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 11px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: @color-border;
    position: absolute;
    top: 10%;
    right: 0;
  }
}

.table-item--checkbox {
  max-width: 42px;
  padding-left: 10px;
}

.table-item--file {
  flex-grow: 1;
  max-width: 100%;
}

.table-item--size {
  max-width: 95px;
  &::after {
    display: none;
  }
}

// переход на тариф

.personal-area-rate-popup {
  width: 720px;
  height: 580px;
  padding: 60px 80px;
  margin-left: -360px;
}

.personal-area-rate-popup__form {
  text-align: center;
}

.personal-area-rate-popup__big-txt {
  font-size: 25px;
  line-height: 35px;
  padding: 0 70px;
  margin-bottom: 40px;
}

.personal-area-rate-popup__small-txt {
  span {
    display: block;
  }
}

.personal-area-rate-popup__sum {
  font-size: 50px;
  line-height: 1;
  color: @blue;
  font-weight: 300;
  margin-top: 10px;
  margin-bottom: 10px;
}

.personal-area-rate-popup__replenishment {
  display: block;
  width: 202px;
  margin: 0 auto;
  margin-bottom: 32px;
}

.personal-area-rate-popup__terms {
  margin-bottom: 15px;

  a {
    color: @blue;
    text-decoration: underline;
  }
}

.personal-area-rate-popup__consent {
  margin-bottom: 15px;
}

.personal-area-rate-popup__submit {
  background-color: transparent;
}

