.logs {
  .projects__form-wrapper--responsible-status {
    .cd-dropdown {
      max-width: 384px;
    }
  }

  .projects__checkboxes-wrapper {
    padding-top: 7px;
    margin-right: 25px;
  }

  .projects__employee-reset-btn {
    margin-left: auto;
    max-width: 384px;
    width: 100%;
  }
}

.logs__top {
  background-color: @color-background;
  padding-bottom: 20px;
}

.logs__title {
  padding-bottom: 20px;
}

.logs__workspace {
  padding-top: 0;
}

.logs__table {
  border-bottom: 1px solid @color-border;
  margin-bottom: 20px;

  @media screen and (max-width: @medium) {
    width: 96vw;
    overflow-x: scroll;
  }

  @media screen and (max-width: @small) {
    overflow-x: auto;
    width: calc(100% + 30px);
    margin-left: -15px;
    border-bottom: none;
  }
}

.logs__table-title-row {
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(0deg, #e6eff3 0, #f4f8fa 100%);
  min-height: 54px;
  border-radius: 3px;

  @media screen and (max-width: @medium) {
    width: 1170px;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    display: none;
  }
}

.logs__table-title-item {
  font-weight: 700;
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 17px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #ffffff;
    position: absolute;
    top: 10%;
    right: 0;
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}

.logs__table-title-item--date,
.logs__table-item--date {
  max-width: 108px;
}

.logs__table-title-item--project,
.logs__table-item--project {
  max-width: 216px;
}

.logs__table-title-item--event,
.logs__table-item--event {
  max-width: 148px;
}

.logs__table-title-item--dscr,
.logs__table-item--dscr {
  max-width: 100%;
  flex-grow: 1;
}

.logs__table-title-item--executor,
.logs__table-item--executor {
  max-width: 228px;
}

.logs__table-row {
  display: flex;
  justify-content: flex-start;
  min-height: 43px;
  border-bottom: 1px solid @color-border;
  border-left: 1px solid @color-border;
  border-right: 1px solid @color-border;

  &:last-child {
    border-bottom: 1px solid transparent;
  }

  @media screen and (max-width: @medium) {
    width: 1170px;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    width: 100%;
    border: none;
  }
}

.logs__table-item {
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 11px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: @color-border;
    position: absolute;
    top: 10%;
    right: 0;
  }

  &:last-child {
    &::after {
      display: none;
    }
  }

  @media screen and (max-width: @small) {
    display: flex;
    justify-content: space-between;
    max-width: 100%;
    padding-right: 20px;
    padding-bottom: 13px;
    border-bottom: 1px solid @color-border;

    &::after {
      display: none;
    }

    span:nth-child(2) {
      width: 69%;
    }

    &:first-child {
      background-image: linear-gradient(0deg,#e6eff3 0,#f4f8fa 100%);
      font-weight: bold;
    }
  }
}

.logs__table-item--date {
  max-width: 108px;

  @media screen and (max-width: @small) {
    max-width: 100%;
    width: 100%;
  }
}

.logs__table-item--project {
  max-width: 216px;

  @media screen and (max-width: @small) {
    max-width: 100%;
    width: 100%;
  }
}

.logs__table-item--event {
  max-width: 148px;

  @media screen and (max-width: @small) {
    max-width: 100%;
    width: 100%;
  }
}

.logs__table-item--dscr {
  max-width: 100%;
  flex-grow: 1;
  padding-right: 20px;
  padding-bottom: 10px;
}

.logs__table-item--executor {
  max-width: 228px;

  @media screen and (max-width: @small) {
    max-width: 100%;
    width: 100%;
  }
}

.logs__mobile-title-txt {
  display: none;

  @media screen and (max-width: @small) {
    display: block;
    font-weight: bold;
    width: 30%;
  }
}
