.support {

}

.support__wrapper {
  padding-top: 30px;
}

.support__title {
  margin-bottom: 20px;
}

.support__wrapper-inner {
  width: 840px;
  margin: 0 auto;

  @media screen and (max-width: @medium) {
    width: 100% !important;
  }
}

.support__chat {
  height: 400px;
  width: 838px !important;
  background-color: @color-background;
  border: 1px solid @color-border;
  border-radius: 3px 3px 0 0;
  display: flex;
  flex-direction: column;

  @media screen and (max-width: @medium) {
    width: 100% !important;
  }
}

.jspPane {
  padding-left: 26px !important;
  padding-right: 26px !important;
  padding-bottom: 26px !important;
}

.support__chat-date {
  font-weight: bold;
  color: #636362;
  padding-top: 13px;
  padding-bottom: 17px;
  text-align: center;
}

.support__chat-message {
  color: #636362;
  max-width: 70%;
  background-color: #e0f3fd;
  padding: 6px 15px;
  border-radius: 3px 3px 3px 0;
  margin: 0 10px;
  margin-bottom: 10px;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    border: 5px solid transparent;
    border-right: 5px solid #e0f3fd;
    border-bottom: 5px solid #e0f3fd;
  }

  @media screen and (max-width: @medium) {
    max-width: 100%;
  }
}

.support__chat-message--user {
  background-color: #ffffff;
  margin-left: auto;

  &::after {
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    border: 5px solid transparent;
    border-right: 5px solid #ffffff;
    border-bottom: 5px solid #ffffff;
  }
}

.support__chat-txt {
  width: 90%;
}

.support__chat-time {
  color: @blue;
  text-align: right;
}

.support__form {
  width: 838px;
  display: flex;

  .form-project-file-comment__input-text {
    border-radius: 0 0 3px 3px;
    border-top: none;
    border-right: none;
  }

  @media screen and (max-width: @medium) {
    width: 100%;

    .form-project-file-comment__submit-btn {
      position: relative;
      font-size: 0;
      width: 50px;

      &::after {
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        position: absolute;
        top: 10px;
        left: 11px;
        background-image: url(../img/icons/send-mess-mobile-icon.png);
        background-size: cover;
        background-position: 50%;
        background-repeat: no-repeat;
      }
    }
  }
}
