.settings {

}

.settings__wrapper {
  padding-top: 30px;
  padding-bottom: 30px;
}

.settings__title {
  margin-bottom: 20px;
}

.settings__form {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}

.settings__form-fieldset {
  max-width: 378px;
  width: 100%;
  border: none;
  background-color: @color-background;
  padding: 0;
  padding-bottom: 30px;
  border-radius: 3px;

  .cd-dropdown {
    max-width: 338px;
  }

  .cd-active {
    max-width: 338px;
  }

  legend {
    font-size: 21px;
    line-height: 1;
    padding-left: 20px;
    padding-top: 12px;
    padding-bottom: 16px;
    width: 100%;
    background-image: linear-gradient(0deg,#e6eff3 0,#f4f8fa 100%);
    margin-bottom: 30px;
    border-radius: 3px 3px 0 0;
  }

  @media screen and (max-width: @medium) {
    max-width: 100%;

    .cd-dropdown {
      max-width: 100%;
    }

    .cd-active {
      max-width: 100%;
    }
  }
}

.settings_bold-txt {
  display: block;
  font-weight: bold;
  margin-bottom: 10px;
}

.settings__form-fieldset-wrapper {
  padding: 0 20px;
}

.settings__file-wrapper {
  height: 132px;
  border-radius: 3px;
  border: 1px solid @color-border;
  padding-top: 21px;
  margin-bottom: 20px;
  background-color: #ffffff;
  position: relative;

  @media screen and (max-width: @medium) {
    border: 1px solid @blue;
    height: 40px;
    padding-top: 9px;
  }
}

.settings__file-txt {
  display: block;
  text-align: center;
  width: 148px;
  margin: 0 auto;
  margin-bottom: 15px;

  @media screen and (max-width: @medium) {
    display: none;
  }
}

.settings__file-blue-txt {
  display: block;
  text-align: center;
  width: 148px;
  margin: 0 auto;
  font-weight: bold;
  color: @blue;

  @media screen and (max-width: @medium) {
    max-width: 100%;
  }
}

.settings__file-input {
  cursor: pointer;
  position: absolute;
  width: 100%;
  font-size: 0;
  opacity: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.settings__color-wrapper {
  margin-bottom: 20px;
}

.settings__text-wrapper {
  margin-bottom: 20px;
}

.settings__text-input {
  width: 100%;
}

.settings__xls-file-wrapper {
  margin-bottom: 20px;
}

.settings__dowmload-links-wrapper {
  display: flex;
  justify-content: space-between;
}

.settings__download {
  font-weight: bold;
  color: inherit;
  border-bottom: 2px solid @blue;
}

.settings__replace {
  font-weight: bold;
  color: inherit;
  border-bottom: 2px solid @blue;
}

.settings__restore {
  font-weight: bold;
  color: inherit;
  border-bottom: 2px solid @blue;
}

.settings--active-link {
  color: @blue;
}

.settings--disabled-link {
  opacity: 0.7;
}

.settings__form-btn {
  width: 100%;
  background-color: transparent;
}

.settings__text-wrapper-inner {
  display: flex;

  label {
    margin: 0 10px;
    align-items: center;
    padding-top: 5px;
  }
}

.settings__sub-title {
  font-size: 21px;
  line-height: 1;
  margin-bottom: 10px;
}

.settings__failed-message-status {
  margin-bottom: 20px;
}

.settings__failed-message-time-interval {
  margin-bottom: 20px;
}

.settings__end-workday {
  margin-bottom: 20px;
}

.settings__submit-btn {
  flex-grow: 1;
  min-height: 40px;
  border: none;
  border-radius: 3px;
  margin-top: 30px;
  font-weight: bold;
}
