.project-settings {

}

.project-settings__wrapper {
  padding-top: 40px;
}

.project-settings__title {
  width: 100%;
  max-width: 800px;
  border: 1px solid @color-border;
  border-radius: 3px 3px 0 0;
  padding-left: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  position: relative;

  @media screen and (max-width: @small) {
    border: none;
    padding-left: 0;
    padding-bottom: 10px;
    padding-top: 0;
  }
}

.project-settings__wrapper-inner {
  border: 1px solid @color-border;
  margin-top: -1px;

  @media screen and (max-width: @small) {
    border: none;
  }
}
