.clients__wrapper {
  width: 100%;
  margin-bottom: 30px;
}

.clients__top {
  background-color: @color-background;
  padding-top: 30px;
  padding-bottom: 20px;
}

.clients__bottom {
}

.clients__title {
  margin-bottom: 15px;
}

.clients__projects-btn-wrapper {
  border: 1px solid @color-border;
  border-radius: 3px;
  min-height: 82px;
  box-sizing: border-box;
  padding-left: 14px;
  padding-top: 26px;
  padding-right: 14px;
  padding-bottom: 16px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-content: space-between;

  @media screen and (max-width: @small) {
    padding-top: 20px;
    padding-left: 0;
    border: none;
    flex-wrap: nowrap;
    width: 93vw;
    overflow-x: scroll;
    min-height: 60px;
    margin-bottom: 10px;
  }
}

.clients__workspace-controls {
  @media screen and (max-width: @small) {
    flex-wrap: wrap;
  }

  .controls__add-btn {
    width: 200px;
    text-align: center;

    @media screen and (max-width: @small) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  .controls__edit-txt-btn {
    width: 200px;
    text-align: center;
    pointer-events: none;

    @media screen and (max-width: @small) {
      border-radius: 50%;
      border: 1px solid #66a0e0;
      width: 40px;
      height: 40px;
      position: relative;
      font-size: 0;
      margin-left: auto;

      &::after {
        content: "";
        display: block;
        width: 18px;
        height: 18px;
        background-image: url(../img/icons/edit-btn-icon.png);
        background-size: cover;
        background-position: 50%;
        background-repeat: no-repeat;
        position: absolute;
        top: 10px;
        left: 10px;
      }
    }
  }

  .controls__exp-xls {
    pointer-events: none;

    @media screen and (max-width: @small) {
      border-radius: 50%;
      border: 1px solid #66a0e0;
      width: 40px;
      height: 40px;
      position: relative;
      font-size: 0;

      &::after {
        content: "";
        display: block;
        width: 18px;
        height: 18px;
        background-image: url(../img/icons/export-btn-icon.png);
        background-size: cover;
        background-position: 50%;
        background-repeat: no-repeat;
        position: absolute;
        top: 10px;
        left: 10px;
      }
    }
  }

  .close-btn {
    margin-left: auto;
    pointer-events: none;

    @media screen and (max-width: @small) {
      margin-left: 0;
      margin-right: auto;
    }
  }
}

.clients__workspace-controls--active {
  .controls__edit-txt-btn,
  .controls__exp-xls,
  .close-btn {
    pointer-events: auto;
  }
}

.clients__form-wrapper {
  background-color: @color-background;
  border-bottom: 1px solid @color-border;

  @media screen and (max-width: @medium) {
    width: 1170px;
  }
}

.clients__form {
  display: flex;
  justify-content: flex-end;
  min-height: 64px;
}

.clients__form-item-wrapper {
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 11px;
  position: relative;

  &:last-child {
    flex-grow: 1;
  }
}

.clients__form-input {
  display: block;
  width: 100%;
  border-radius: 3px;
  border: 1px solid @color-border;
  padding: 5px 8px;
  margin-left: auto;
}

.clients__table {
  border-bottom: 1px solid @color-border;
  margin-bottom: 20px;

  @media screen and (max-width: @medium) {
    padding-top: 20px;
    padding-left: 0;
    flex-wrap: nowrap;
    width: 93vw;
    overflow-x: scroll;
  }
}

.clients__table-title-row {
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(0deg, #e6eff3 0, #f4f8fa 100%);
  min-height: 54px;
  border-radius: 3px;

  @media screen and (max-width: @medium) {
    width: 1170px;
  }
}

.clients__table-title-item {
  font-weight: 700;
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 17px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #ffffff;
    position: absolute;
    top: 10%;
    right: 0;
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}

.clients__table-row {
  display: flex;
  justify-content: flex-start;
  min-height: 43px;
  border-bottom: 1px solid @color-border;
  border-left: 1px solid @color-border;
  border-right: 1px solid @color-border;

  &:last-child {
    border-bottom: 1px solid transparent;
  }

  @media screen and (max-width: @medium) {
    width: 1170px;
  }
}

.clients__table-item {
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 11px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: @color-border;
    position: absolute;
    top: 10%;
    right: 0;
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}

.table-item--checkbox {
  max-width: 42px;
  padding-left: 10px;
}

.clients__new-client {
  display: none;
}

.clients__edit-txt--disabled {
  border-color: #dce8ef;
  color: #dce8ef;
}
