.form-project-finance__wrapper-inner {
  border-top: 1px solid @color-border;
  padding-top: 30px;
}

.form-project-finance__date-wrapper {
  max-width: 382px;
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @small) {
    width: 100%;
    margin-bottom: 10px;
  }
}

.form-project-finance__today-checkbox {
  @media screen and (max-width: @small) {
    margin-bottom: 10px;
  }
}

.form-project-finance__wrapper-inner-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 32px;

  .cd-dropdown {
    width: 100%;
    max-width: 218px;
  }

  @media screen and (max-width: @medium) {
    flex-wrap: wrap;

    .cd-dropdown {
      width: 100%;
      max-width: 49%;
      margin-bottom: 20px;
    }
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;
    padding-left: 15px;
    padding-right: 15px;

    .cd-dropdown {
      max-width: 100%;
      margin-bottom: 10px;
    }
  }
}

.form-project-finance__exp-xls {
  margin-right: 0;
}

.form-project-finance__wrapper-inner-middle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;

  @media screen and (max-width: @medium) {
    flex-wrap: wrap;
  }

  @media screen and (max-width: @small) {
    flex-wrap: wrap;
    margin-bottom: 0;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.form-project-finance__btn {
  width: 100%;
  max-width: 148px;
  border-radius: 3px;
  border: 1px solid #66a0e0;
  font-weight: 700;
  margin-right: 20px;
  color: #66a0e0;
  padding: 10px 14px;
  background-color: transparent;

  @media screen and (max-width: @small) {
    margin-right: 0;
    flex-grow: 1;
    max-width: 49%;
    margin-bottom: 10px;
  }
}

.form-project-finance__btn--coming {
  color: #40bf70;
  border-color: #40bf70;

  @media screen and (max-width: @medium) {
    flex-grow: 1;
    max-width: 30%;
  }
}

.form-project-finance__btn--consumption {
  color: #e30613;
  border-color: #e30613;

  @media screen and (max-width: @medium) {
    flex-grow: 1;
    max-width: 30%;
  }
}

.form-project-finance__btn--calculate {
  color: #dce8ef;
  border-color: #dce8ef;

  @media screen and (max-width: @medium) {
    flex-grow: 1;
    max-width: 30%;
  }

  @media screen and (max-width: @small) {
    max-width: 100%;
    margin-bottom: 10px;
  }
}

.form-project-finance__info {
  padding-left: 20px;
  padding-right: 20px;
  border: 1px solid  @color-border;
  background-color: @color-background;
  min-height: 40px;
  border-radius: 3px;
  width: 100%;
  max-width: 562px;
  display: flex;
  justify-content: space-between;
  padding-top: 9px;

  @media screen and (max-width: @small) {
    flex-direction: column;
    position: relative;
    padding-bottom: 10px;
    margin-bottom: 10px;
  }
}

.form-project-finance__info-wrapper {
  display: inline-block;
}

.form-project-finance__info-sum--minus {
  &::before {
    content: "-";
  }
}

.form-project-finance__info-sum--plus {
  &::before {
    content: "+";
  }
}

.form-project-finance__info-sum--green {
  color: #40bf70;
}

.form-project-finance__info-sum--red {
  color: #e30613;
}

.form-project-finance__info-sum--rub {
  &::after {
    content: "\20BD"
  }
}

.form-project-finance__info-sum--usa {
  &::after {
    content: "\0024"
  }
}

.form-project-finance__info-sum--eur {
  &::after {
    content: "\20AC"
  }
}

.form-project-finance__info-btn-close {
  height: 14px;
  width: 14px;
  display: inline-block;
  background-image: url(../img/icons/btn-close-icon.png);
  background-position: 50%;
  background-size: cover;
  background-repeat: no-repeat;
  border: none;
  background-color: transparent;
  margin-top: 3px;

  @media screen and (max-width: @small) {
    position: absolute;
    top: 10px;
    right: 10px;
  }
}

.form-project-finance__btn-close {
  background-color: transparent;

  @media screen and (max-width: @small) {
    position: relative;
    width: 100%;
    margin-left: 0;
    border-radius: 3px;

    &::after {
      content: "Удалить";
      width: 100%;
      background: 0 0;
      font-weight: 700;
      text-align: center;
      color: #e30613;
    }
  }
}

.form-project-finance__table-title-row {
  display: flex;
  justify-content: flex-start;
  background-image: linear-gradient(0deg,#e6eff3 0,#f4f8fa 100%);
  min-height: 54px;
  border-radius: 3px;

  @media screen and (max-width: @medium) {
    width: 1170px;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    background: none;

    .table-item--checkbox {
      display: block;
      max-width: 100%;

      .form-project-finance__mobile-title-txt {
        display: inline;
      }
    }
  }
}

.form-project-finance__table-title-item {
  font-weight: 700;
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 17px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: #fff;
    position: absolute;
    top: 10%;
    right: 0;
  }

  @media screen and (max-width: @small) {
    display: none;
  }
}

.form-project-finance__table-row {
  display: flex;
  justify-content: flex-start;
  min-height: 43px;
  border-bottom: 1px solid @color-border;

  &:last-child {
    border-bottom: 1px solid transparent;

    @media screen and (max-width: @small) {
      margin-bottom: 0;
    }
  }

  @media screen and (max-width: @medium) {
    width: 1170px;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    margin-bottom: 20px;
    border-bottom: 0;
  }
}

.form-project-finance__table-item {
  width: 100%;
  max-width: 272px;
  box-sizing: border-box;
  padding-left: 21px;
  padding-top: 11px;
  position: relative;

  &::after {
    content: "";
    display: block;
    height: 80%;
    width: 1px;
    background-color: @color-border;
    position: absolute;
    top: 10%;
    right: 0;
  }

  @media screen and (max-width: @small) {
    max-width: 100%;
    border-bottom: 1px solid @color-border;
    padding-top: 11px;
    padding-bottom: 9px;
    padding-right: 15px;
    display: flex;
    justify-content: space-between;

    &::after {
      display: none;
    }

    &.table-item--checkbox {
      border-bottom: none;
      position: absolute;
      z-index: 10;
      width: 320px;
    }

    &.table-item--date {
      background-image: linear-gradient(0deg,#e6eff3 0,#f4f8fa 100%);
      padding-left: 35px;
      font-weight: bold;
    }

    .form-project-finance__mobile-title-txt {
      display: block;
      border-right: 1px solid @color-border;
      width: 100%;
      max-width: 40%;
      font-weight: bold;
    }
  }
}

.table-item--checkbox {
  max-width: 42px;
  padding-left: 10px;
}

.table-item--date {

}

.table-item--sum {

}

.table-item--comment {
  flex-grow: 1;
}

.table-item--name {
  flex-grow: 1;

  &::after {
    display: none;
  }
}

.form-project-finance__table-wrapper {
  @media screen and (max-width: @medium) {
    width: 96vw;
    overflow-x: scroll;
  }
}
