.type-projects {
  .cd-dropdown {
    display: none;

    @media screen and (max-width: @small) {
      display: block;
      max-width: 100%;
    }
  }
}

.type-projects__wrapper {
  width: 100%;
  margin-bottom: 30px;
}

.type-projects__top {
  background-color: @color-background;
  padding-top: 30px;
  padding-bottom: 30px;

  @media screen and (max-width: @small) {
    padding-bottom: 10px;
  }
}

.type-projects__bottom {
  padding-top: 30px;
}

.type-projects__title {
  margin-bottom: 15px;
}

.type-projects__projects-btn-wrapper {
  border: 1px solid @color-border;
  border-radius: 3px;
  min-height: 82px;
  box-sizing: border-box;
  padding-left: 14px;
  padding-top: 26px;
  padding-right: 14px;
  padding-bottom: 16px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-content: space-between;

  @media screen and (max-width: @small) {
    padding: 0;
    border: none;
    min-height: 38px;

    .projects__form-area-btn {
      display: none;
    }
  }
}

.type-projects__workspace-controls {
  .controls__edit-btn {
    margin-left: auto;
    margin-right: 20px;
  }

  @media screen and (max-width: @small) {
    flex-wrap: wrap;

    .controls__add-btn {
      width: 100%;
      margin-right: 0;
      text-align: center;
      margin-bottom: 15px;

      &::before {
        left: 25%;
      }
    }
  }
}

.type-projects__result {
  @media screen and (max-width: @small) {
    width: 93vw;
    overflow-x: scroll;
  }
}

.result-type-projects__title-row {
  @media screen and (max-width: @small) {
    width: 900px;
  }
}

.result-type-projects__info-list {
  @media screen and (max-width: @small) {
    width: 900px;
  }
}
