.edit-btn {
  border-radius: 50%;
  border: 1px solid #66a0e0;
  width: 40px;
  height: 40px;
  position: relative;
}
.edit-btn::after {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-image: url(../img/icons/edit-btn-icon.png);
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  position: absolute;
  top: 10px;
  left: 10px;
}
.edit-btn:hover {
  opacity: 0.7;
}
.edit-btn--disabled {
  border: 1px solid #dce8ef !important;
}
.edit-btn--disabled::after {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-image: url(../img/icons/edit-btn-icon-disabled.png);
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  position: absolute;
  top: 10px;
  left: 10px;
}
.close-btn {
  border-radius: 50%;
  border: 1px solid #dce8ef;
  width: 40px;
  height: 40px;
  position: relative;
}
.close-btn::after {
  content: "";
  display: block;
  width: 20px;
  height: 18px;
  background-image: url(../img/icons/btn-close-icon.png);
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  position: absolute;
  top: 10px;
  left: 9px;
}
.close-btn:hover {
  opacity: 0.7;
}
.close-btn--red {
  border-color: #e30613;
}
.close-btn--red::after {
  content: "";
  display: block;
  width: 20px;
  height: 18px;
  background-image: url(../img/icons/btn-close-icon-red.png);
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  position: absolute;
  top: 10px;
  left: 9px;
}
.form__fieldset {
