  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: url(../img/icons/select-arrow-icon.png);
  background-size: 12px 7px;
  background-position: calc(100% - 14px) 12px;
  background-repeat: no-repeat;
}
.form__select option {
  font-size: 15px;
  line-height: 22px;
