.form__option {
  position: relative;
}
.form__option::after {
  content: "";
  display: block;
  width: 11px;
  height: 11px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 5px;
  border-radius: 50%;
}
.form__option--not-active::after {
  background-color: #dcdcdc;
}
.form__option--waiting-activation::after {
  background-color: #626262;
}
.form__option--waiting-completion::after {
