    display: block;
    height: 25px;
    width: 30px;
    position: relative;
    border: none;
    padding: 0;
    background-color: transparent;
    order: 3;
    margin-bottom: 15px;
  }
}
.header__mobile-btn--close {
  animation: scale2 0.4s;
}
.header__mobile-btn--close::after {
  content: "";
  display: block;
  width: 28px;
  height: 3px;
  background-color: #66a0e0;
  position: absolute;
  top: 11px;
  left: 0;
  transform: rotate(45deg);
  border-radius: 1px;
}
.header__mobile-btn--close::before {
  content: "";
  display: block;
  width: 28px;
  height: 3px;
  background-color: #66a0e0;
  position: absolute;
  bottom: 11px;
  left: 0;
  transform: rotate(135deg);
  border-radius: 1px;
}
.header__mobile-btn--close .header__mobile-btn-decor {
  display: none;
}
.header__mobile-btn--gamburger {
  animation: scale 0.3s;
}
@media screen and (max-width: 768px) {
  .header__mobile-btn--gamburger {
    height: 25px;
    width: 30px;
  }
  .header__mobile-btn--gamburger::after {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: #66a0e0;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1px;
  }
  .header__mobile-btn--gamburger::before {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: #66a0e0;
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 1px;
  }
  .header__mobile-btn--gamburger .header__mobile-btn-decor {
    display: block;
    width: 80%;
    height: 3px;
    background-color: #66a0e0;
    position: absolute;
    top: 11px;
    right: 0;
    border-radius: 1px;
  }
}
@keyframes scale {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes scale2 {
  0% {
    transform: scale(0);
  }
  70% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
