.header {
  background-color: #f4f8fa;

  @media screen and (max-width: @medium) {

  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 70px;
  width: 100%;

  @media screen and (max-width: @medium) {
    flex-wrap: wrap;
    justify-content: flex-start;
    padding-top: 15px;
    min-height: 50px;
  }
}

.header__logo-link {
  margin-right: 30px;

  @media screen and (max-width: @medium) {
    order: 1;
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.header__logo-img {
  @media screen and (max-width: @medium) {
    width: 60px;
    height: 25px;
  }
}

.header__nav {
  margin-right: 55px;

  @media screen and (max-width: @medium) {
    display: none;
    order: 4;
    width: calc(100% + 30px);
    background-color: #fafcfd;
    margin: 0 -15px;
  }
}

.header__nav-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 70px;

  @media screen and (max-width: @medium) {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 0px;
    min-height: 36px;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    padding-left: 45px;
    padding-right: 15px;
    padding-top: 18px;
  }
}

.header__nav-list-item {
  margin-right: 22px;
  min-height: 70px;
  border-bottom: 3px solid transparent;
  box-sizing: border-box;
  padding-top: 24px;

  &:last-child {
    margin-right: 0;

    @media screen and (max-width: @small) {
      padding-bottom: 36px;
    }
  }

  @media screen and (max-width: @medium) {
    width: 100%;
    margin-right: 0;
    text-align: center;
    font-size: 14px;
    padding-top: 12px;
    padding-bottom: 12px;
    min-height: 24px;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    margin-right: 0;
    text-align: left;
    font-size: 26px;
    padding-top: 18px;
    padding-bottom: 18px;
  }
}

.header__nav-list-item--active {
  border-bottom: 3px solid @blue;

  .header__nav-list-link {
    color: @color-text;
  }

  @media screen and (max-width: @medium) {
    border-bottom: none;

    .header__nav-list-link {
      color: @blue;
    }
  }
}

.header__nav-list-link {
  color: #808e96;

  &:hover,
  &:focus {
    color: @color-text;
  }

  &:active {
    opacity: 0.7;
  }
}

.header__settings {
  position: relative;
  padding-left: 50px;
  width: 100%;
  max-width: 132px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &::after {
    content: "";
    display: block;
    height: 100%;
    width: 1px;
    background-color: #808e96;
    position: absolute;
    top: 0;
    left: 0;
  }

  @media screen and (max-width: @medium) {
    display: none;
    order: 5;
    max-width: 430px;
    padding-left: 15px;
    padding-top: 0;

    &::after {
      display: none;
    }
  }

  @media screen and (max-width: @small) {
    display: none;
    order: 5;
    width: calc(100% + 30px);
    max-width: calc(100% + 30px);
    background-color: #fafcfd;
    margin: 0 -15px;
    border-top: 1px solid @color-border;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 45px;
    padding-top: 40px;

    &::after {
      display: none;
    }
  }
}

.header__settings-item {
  margin-right: 30px;
  color: @color-text;

  &:last-child {
    margin-right: 0;
  }

  @media screen and (max-width: @medium) {
    display: inline-block;
    vertical-align: middle;
  }

  @media screen and (max-width: @small) {
    display: block;
    margin-bottom: 30px;
  }
}

.header__settings-link {
  display: flex;
  align-items: center;
}

.header__settings-link-icon {
  margin-right: 20px;
}

.header__settings-txt {
  display: none;

  @media screen and (max-width: @medium) {
    display: inline;
    color: #808e96;
  }
}

.header__date-time {
  margin-left: 40px;

  @media screen and (max-width: @medium) {
    order: 2;
    margin-right: auto;
    margin-left: 38%;
    padding-bottom: 15px;
  }

  @media screen and (max-width: @small) {
    order: 2;
    margin-right: auto;
    margin-left: 24%;
    padding-bottom: 15px;
  }
}

.header__time {
  font-size: 30px;
  font-weight: 300;
  line-height: 1;
  color: #808e96;
  text-align: center;

  @media screen and (max-width: @medium) {
    font-size: 22px;
  }
}

.header__date {
  font-size: 12px;
  font-weight: 300;
  line-height: 18px;
  color: #808e96;
  letter-spacing: 2.6px;
  text-align: center;

  @media screen and (max-width: @medium) {
    font-size: 9px;
    line-height: 9px;
  }
}

.header__user-block {
  margin-left: auto;
  border-radius: 50%;
  width: 42px;
  height: 42px;

  @media screen and (max-width: @medium) {
    display: none;
    order: 5;
    width: 200px;
    height: 52px;
    padding-top: 10px;
  }

  @media screen and (max-width: @small) {
    order: 5;
    margin: 0 -15px;
    display: none;
    width: calc(100% + 30px);
    padding-left: 40px;
    border-radius: 0;
    padding-bottom: 20px;
    min-height: 60px;
    background-color: #fafcfd;
  }
}

.header__user-link {
  display: block;
  width: 42px;
  height: 42px;

  @media screen and (max-width: @medium) {
    display: inline-block;
    vertical-align: middle;
    height: 38px;
  }

  @media screen and (max-width: @small) {
    display: inline;
    vertical-align: top;
    margin-right: 20px;
  }
}

.header__user-img {
  width: 100%;
  height: auto;
  object-fit: cover;

  @media screen and (max-width: @medium) {
    width: 35px;
    height: 35px;
  }
}

.header__user-txt {
  display: none;

  @media screen and (max-width: @medium) {
    display: inline;
    width: 100%;
    vertical-align: middle;
    line-height: 34px;
    color: #808e96;

    a {
      color: #808e96;
    }
  }
}

.header__mobile-btn {
  display: none;
  outline: none;

  @media screen and (max-width: @medium) {
    display: block;
    height: 25px;
    width: 30px;
    position: relative;
    border: none;
    padding: 0;
    background-color: transparent;
    order: 3;
    margin-bottom: 15px;
  }
}

.header__mobile-btn--close {
  animation: scale2 .4s;

  &::after {
    content: "";
    display: block;
    width: 28px;
    height: 3px;
    background-color: @blue;
    position: absolute;
    top: 11px;
    left: 0;
    transform: rotate(45deg);
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 28px;
    height: 3px;
    background-color: @blue;
    position: absolute;
    bottom: 11px;
    left: 0;
    transform: rotate(135deg);
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: none;
  }
}

.header__mobile-btn--gamburger {
  animation: scale 0.3s;

  @media screen and (max-width: @medium) {

    height: 25px;
    width: 30px;

    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 3px;
      background-color: @blue;
      position: absolute;
      top: 0;
      left: 0;
      border-radius: 1px;
    }

    &::before {
      content: "";
      display: block;
      width: 100%;
      height: 3px;
      background-color: @blue;
      position: absolute;
      bottom: 0;
      left: 0;
      border-radius: 1px;
    }

    .header__mobile-btn-decor {
      display: block;
      width: 80%;
      height: 3px;
      background-color: @blue;
      position: absolute;
      top: 11px;
      right: 0;
      border-radius: 1px;
    }
  }
}

@keyframes scale {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes scale2 {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
