.finance {
  .projects__form-wrapper--responsible-status {
    .cd-dropdown {
      max-width: 384px;
    }
  }

  .projects__checkboxes-wrapper {
    padding-top: 7px;
    margin-right: 25px;
  }

  .projects__employee-reset-btn {
    margin-left: auto;
    max-width: 384px;
    width: 100%;
  }
}

.finance__title {
  margin-bottom: 20px;
}

.finance__form {
  .form-project-finance__wrapper-inner {
    border-top: none;
    padding-top: 10px;
  }

  .form-project-finance__wrapper-inner-middle {
    padding: 0;
    justify-content: flex-start;

    @media screen and (max-width: @medium) {
      flex-wrap: wrap;
      position: relative;
    }

    @media screen and (max-width: @small) {
      flex-direction: column;
    }
  }

  .controls__exp-xls {
    margin-right: 20px;

    @media screen and (max-width: @small) {
      width: 100%;
      margin-right: 0;
      text-align: center;
      margin-bottom: 10px;
    }
  }

  .form-project-finance__btn-close {
    margin-left: auto;

    @media screen and (max-width: @medium) {
      position: absolute;
      top: 0;
      right: 0;
    }

    @media screen and (max-width: @small) {
      position: relative;
      width: 100%;
      margin-left: 0;
      border-radius: 3px;

      &::after {
        content: "Удалить";
        width: 100%;
        background: 0 0;
        font-weight: 700;
        text-align: center;
        color: #e30613;
      }
    }
  }

  .form-project-finance__table-title-row {
    border-radius: 3px 3px 0 0;
    border-bottom: 1px solid @color-border;

    @media screen and (max-width: @medium) {
      width: 1170px;
    }
  }

  .form-project-finance__table-row {
    border-left: 1px solid @color-border;
    border-right: 1px solid @color-border;
    border-bottom: 1px solid @color-border;

    @media screen and (max-width: @medium) {
      width: 1170px;
    }

    @media screen and (max-width: @small) {
      width: 100%;
      flex-direction: column;
      margin-bottom: 20px;
    }
  }
}

.finance__workspace-wrapper {
  padding-bottom: 30px;
}

.form-project-finance__mobile-title-txt {
  display: none;
}

.form-project-finance__info {
  @media screen and (max-width: @medium) {
    margin-top: 10px;
  }
}

.form-project-finance__btn--calculate {
  line-height: 21px;
}

.form-project-finance__table-wrapper {
  @media screen and (max-width: @medium) {
    width: 96vw;
    overflow-x: scroll;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    overflow-x: auto;
  }
}

@media screen and (max-width: @small) {
  .finance {
    .projects__employee-reset-btn {
      max-width: 70px;
    }

    .projects__form-wrapper-inner {
      display: flex;
      justify-content: space-between;
      margin-bottom: 20px;
    }

    .projects__form-fieldset-type {
      margin-bottom: 0;
    }

    .projects__form-wrapper--responsible-status {
      .cd-dropdown {
        margin-bottom: 0;
      }
    }

    .projects__form-wrapper--employee-reset {
      flex-wrap: wrap;
    }

    .projects__form-wrapper {
      margin-bottom: 0;
    }

    .projects__form-filter-mobile-btn {
      margin-bottom: 0;
    }

    .form-project-finance__btn {
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 10px;
    }

    .form-project-finance__info {
      flex-direction: column;
      position: relative;
      margin-bottom: 10px;
      padding-bottom: 10px;
    }

    .form-project-finance__info-btn-close {
      position: absolute;
      top: 10px;
      right: 10px;
    }

    .form-project-finance__table-title-row {
      background: 0 0;
      width: 100%;
    }

    .form-project-finance__table-title-item {
      display: none;

      &.table-item--checkbox {
        display: block;
        background: 0 0;
        max-width: 93vw;
      }

      .form-project-finance__mobile-title-txt {
        font-weight: 400;
        display: inline;
        border-right: none;
      }
    }

    .table-item--title {
      padding-left: 40px;
      padding-bottom: 15px;
      background-image: linear-gradient(0deg,#e6eff3 0,#f4f8fa 100%);
      display: flex;
      border-top: 1px solid #dce8ef;
      border-bottom: 1px solid #dce8ef;
      max-width: 100%;
    }


    .form-project-finance__table-item {
      max-width: 100%;
      display: flex;
      justify-content: space-between;
      padding-right: 10px;
      padding-left: 10px;
      padding-bottom: 9px;
      border-bottom: 1px solid @color-border;

      &.table-item--title {
        padding-left: 35px;
        border-radius: 0;
        border-top: none;
      }

      &.table-item--name {
        border-bottom: none;
      }

      &::after {
        display: none;
      }
    }

    .form-project-finance__table-item.table-item--checkbox {
      border-bottom: none;
      position: absolute;
      z-index: 10;
      width: 320px;
    }

    .form-project-finance__mobile-title-txt {
      display: block;
      max-width: 40%;
      width: 100%;
      border-right: 1px solid @color-border;
    }
  }
}
