.projects {
  @media screen and (max-width: @small) {
    background-color: #fbfdfe;
  }
}

.projects__wrapper {
  padding-top: 30px;
  width: 100%;

  @media screen and (max-width: @medium) {
    .projects__form-area-projects {
      width: 100%;
      max-width: 100%;
      min-height: 62px;
      padding-top: 20px;
      padding-bottom: 10px;
    }
  }

  @media screen and (max-width: @small) {
    display: flex;
    flex-direction: column;

    .projects__form-area-projects {
      padding-top: 20px;
      padding-left: 0;
      border: none;
      flex-wrap: nowrap;
      width: 93vw;
      overflow-x: scroll;
      min-height: 60px;
      margin-bottom: 10px;
    }

    .projects__form-area-btn {
      min-width: 36px;
      box-sizing: content-box;
      flex-basis: 50px;
    }
  }
}

.projects__title {
  margin-bottom: 15px;

  @media screen and (max-width: @small) {
    order: 1;
    font-size: 30px;
  }
}

.projects__form-filter-mobile-btn {
  display: none;

  @media screen and (max-width: @small) {
    display: block;
    width: 100%;
    border-radius: 3px;
    border: 1px solid #66a0e0;
    font-weight: 700;
    margin-right: 20px;
    color: #66a0e0;
    padding: 9px 14px;
    margin-bottom: 20px;
    background-color: transparent;
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 18px;
      height: 18px;
      position: absolute;
      top: 8px;
      left: 6%;
      background-image: url(../img/icons/filter-mobile-icon.png);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: 50%;
    }
  }
}

.projects__controls {
  @media screen and (max-width: @medium) {
    justify-content: space-around;

    .controls__tile-btn {
      display: none;
    }

    .controls__row-btn {
      display: none;
    }

    .controls__exp-xls {
      display: none;
    }
  }

  @media screen and (max-width: @small) {
    order: 2;
    flex-direction: column;

    &.controls {
      margin-bottom: 0;
    }

    .controls__tile-btn {
      display: none;
    }

    .controls__row-btn {
      display: none;
    }

    .controls__exp-xls {
      display: none;
    }

    .controls__add-btn {
      margin-right: 0;
      width: 100%;
      margin-bottom: 20px;
      text-align: center;

      &::before {
        left: 25%;
      }
    }

    .controls__prijects-type-btn {
      margin-right: 0;
      width: 100%;
      margin-bottom: 20px;
      text-align: center;
    }

    .controls__fields-setting {
      margin-right: 0;
      width: 100%;
      text-align: center;
    }
  }
}

.projects__form {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  @media screen and (max-width: @medium) {
    flex-direction: column;

    .cd-dropdown.cd-active {
      li {
        width: 100% !important;
      }
    }
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    position: relative;
  }
}

.projects__form-fieldset-type {
  width: 100%;
  max-width: 364px;
  min-height: 198px;
  margin-right: 20px;
  border: 1px solid @color-border;
  border-radius: 3px;

  .cd-dropdown {
    max-width: 362px;

    @media screen and (max-width: @medium) {
      display: none;
    }

    @media screen and (max-width: @small) {
      display: none;
    }
  }

  .cd-dropdown > span {
    border: none;
    border-bottom: 1px solid @color-border;
    max-width: 362px;
  }


  @media screen and (max-width: @medium) {
    width: 100%;
    max-width: 100%;
    min-height: 62px;
    margin-bottom: 20px;
  }

  @media screen and (max-width: @small) {
    border: none;
    min-height: 60px;
    margin-bottom: 0;
  }
}

.projects__form-select-type {
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
  margin-bottom: 40px;
}

.projects__form-area-projects {
  box-sizing: border-box;
  padding-top: 40px;
  padding-left: 8px;
  padding-right: 8px;
  padding-bottom: 30px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-content: space-between;
  border-radius: 3px;
  min-height: 162px;
}

.projects__form-area-btn {
  min-width: 40px;
  height: 26px;
  border-radius: 3px;
  border: 1px solid @color-border;
  background-color: transparent;
  text-transform: uppercase;
  position: relative;
  transition: all ease 0.3s;
  text-align: center;

  &:hover,
  &:focus {
    border-color: @blue;
  }

  &:active {
    opacity: 0.7;
  }
}

.projects__btn-counter {
  position: absolute;
  top: -0px;
  right: -0px;
  padding: 4px 4px;
  font-size: 10px;
  line-height: 1;
  background-color: #afb0b0;
  color: #ffffff;
  border-radius: 3px;
  transition: all ease 0.3s;
  margin-top: -10px;
  margin-right: -10px;
}

.projects__form-area-btn-wrapper--active {
  .projects__form-area-btn {
    border-color: @blue;
  }

  .projects__btn-counter {
    background-color: @blue;
  }
}

.projects__form-fieldset-main {
  flex-grow: 1;
  border: none;
  padding: 0;

  @media screen and (max-width: @medium) {
    width: 100%;
  }

  @media screen and (max-width: @small) {
    display: none;
    width: 100%;
    margin: 0 auto;
  }
}

.projects__form-search-input {
  width: 100%;
  margin-bottom: 9px;

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.projects__form-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 9px;

  @media screen and (max-width: @small) {
    order: 3;
  }
}

.projects__form-select-responsible,
.projects__form-select-status,
.projects__form-select-client,
.projects__form-wrapper-inner {
  width: 100%;
  max-width: 368px;
}

.projects__form-wrapper--responsible-status {
  .cd-dropdown:nth-child(2) {
    span:first-child span {
      display: flex;
    }

    ul span:first-child span {
      display: inline-block;
    }
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    margin-bottom: 0;

    .cd-dropdown {
      margin-bottom: 20px;
    }
  }
}

.projects__form-wrapper--client-date {
  @media screen and (max-width: @small) {
    flex-direction: column;

    .cd-dropdown {
      margin-bottom: 20px;
    }

    .projects__form-wrapper-inner {
      display: flex;
      justify-content: space-between;
    }
  }
}

.projects__form-date-input {
  max-width: 176px;
  border-radius: 3px;
  background-image: url(../img/icons/calendar-icon.png);
  background-size: 17px 18px;
  background-position: calc(100% - 17px) 4px;
  background-repeat: no-repeat;

  &::placeholder {
    color: @color-text;
  }

  @media screen and (max-width: @small) {
    max-width: 135px;
    background-position: calc(100% - 12px) 5px;
  }
}

.projects__employees-wrapper {
  box-sizing: border-box;
  padding: 2px 3px;
  min-width: 38px;
  border: 1px solid @color-border;
  border-radius: 3px;
  max-width: 560px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;

  @media screen and (max-width: @medium) {
    display: none;
  }

  @media screen and (max-width: @small) {
    display: none;
  }
}

.projects__employee {
  background-color: #e5f1ff;
  position: relative;
  padding-left: 12px;
  padding-top: 7px;
  padding-right: 28px;
  padding-bottom: 6px;
  margin-right: 3px;
  border-radius: 3px;
}

.projects__employee-name-close {
  display: block;
  width: 10px;
  height: 11px;
  border: none;
  background-color: transparent;
  position: absolute;
  top: 14px;
  right: 9px;
  background-image: url(../img/icons/close-employee-icon.png);
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  transition: all ease 0.3s;

  &:hover,
  &:active {
    opacity: 0.7;
  }

  &:focus {
    opacity: 0.5;
  }
}

.projects__employee-reset-btn {
  border: 1px solid @blue;
  border-radius: 3px;
  padding-left: 26px;
  padding-top: 12px;
  padding-bottom: 11px;
  padding-right: 26px;
  background-color: transparent;
  color: @blue;
  font-weight: bold;
  margin-left: 20px;
  transition: all ease 0.3s;

  &:hover,
  &:active {
    opacity: 0.7;
  }

  &:focus {
    opacity: 0.5;
  }

  .projects__employee-reset-txt-mobi {
    display: none;
  }

  @media screen and (max-width: @medium) {
    margin-left: auto;
  }

  @media screen and (max-width: @small) {
    position: absolute;
    top: 3px;
    right: 0;
    border: none;
    padding: 0;
    margin: 0;

    .projects__employee-reset-txt-mobi {
      display: inline;
    }

    .projects__employee-reset-txt-desc {
      display: none;
    }
  }
}

.projects__employee-close {
  display: none;

  @media screen and (max-width: @small) {
    display: block;
    color: @blue;
    position: absolute;
    top: 3px;
    left: 0;
    font-weight: bold;
    border: none;
    background-color: transparent;
    padding: 0;
    margin: 0;
  }
}

.projects__employee-apply {
  margin-right: 0;
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
  color: #ffffff;
  background-color: #66a0e0;
  padding-top: 12px;
  padding-bottom: 10px;
  font-weight: bold;
  border: 1px solid #66a0e0;
  border-radius: 3px;
  display: none;

  @media screen and (max-width: @medium) {
    display: block;
    width: 200px;
    margin-left: 20px;
  }

  @media screen and (max-width: @small) {
    display: block;
    width: 100%;
    margin-left: 0;
  }
}

.project__mobile-title {
  display: none;

  @media screen and (max-width: @small) {
    background-color: transparent;
    text-align: center;
    width: 100%;
  }
}

.projects__form-wrapper--employee-reset {
  align-items: flex-start;
}

.projects__form-wrapper--checkboxes {
  @media screen and (max-width: @small) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .projects__checkbox-wrapper {
      width: 100%;
    }

    .projects__checkbox-wrapper--check {
      order: 4;
    }

    .projects__checkbox-wrapper--date-add {
      order: 1;
    }

    .projects__checkbox-wrapper--date-control {
      order: 2;
    }

    .projects__checkbox-wrapper--today {
      order: 3;
    }
  }
}

.projects__checkbox-wrapper {
  @media screen and (max-width: @small) {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    padding-top: 10px;
    padding-bottom: 9px;
  }
}

.projects__form-area-btn-wrapper {
  position: relative;
  min-width: 40px;
  height: 26px;
  margin-left: 9px;
  margin-right: 8px;
  margin-bottom: 10px;
}

